*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}
body{
    background-color: #f5f6fa;
}
.app{
    display: flex;
    justify-content: space-evenly;
    padding: 20px;
}
.app__header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
}
.app__header > h1{
    color: red;
}
.app__left{
    flex: 0.9;
}
.app__stats{
    display: flex;
    justify-content: space-between;
}
.app__information > h3 {
    color: #6a5d5d;
    font-weight: 400;
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }
  
  .app__information > h3:last-of-type {
    margin-top: 1rem;
  }

@media (max-width:990px) {
    .app{
        flex-direction: column;
    }
}